		.katalog__infos {
			position: absolute;
			top: 10px;
			left: 10px;
			display: flex;
			align-items: center;
			gap: 7px;
			.katalog__info-main {
				background: #B602F5;
				border-radius: 8px;
				height: 31px;
				display: flex;
				align-items: center;
				padding: 0 10px;
				font-weight: 400;
				font-size: 14px;
				line-height: 125.69%; }
			.katalog__info-dop {
				background: #000000;
				border-radius: 8px;
				height: 31px;
				display: flex;
				align-items: center;
				padding: 0 10px;
				font-weight: 400;
				font-size: 14px;
				line-height: 125.69%; } }
		.katalog__price {
			position: absolute;
			right: 15px;
			bottom: 80px;
			@include mq($max-width: 1359px) {
				right: 5px;
				bottom: 75px; }
			@include mq($max-width: 767px) {
				right: 10px;
				bottom: 80px; }
			@include mq($max-width: 735px) {
				right: 15px;
				bottom: 85px; }
			@include mq($max-width: 374px) {
				right: 5px;
				bottom: 75px; }
			&-main {
				font-weight: 500;
				font-size: 24px;
				line-height: 125.69%; }
			&-old {
				font-weight: 300;
				font-size: 16px;
				line-height: 125.69%;
				color: #AAAAAA;
				text-decoration: line-through; } }
		.katalog__discount {
			right: 15px;
			bottom: 160px;
			position: absolute;
			display: flex;
			align-items: center;
			justify-content: center;
			min-width: 59px;
			height: 32px;
			background: linear-gradient(108.47deg, #F59502 -0.66%, #F55902 106.05%);
			border-radius: 11px;
			font-weight: 400;
			font-size: 14px;
			line-height: 125.69%;
			@include mq($max-width: 1359px) {
				right: 5px;
				bottom: 140px; }
			@include mq($max-width: 1279px) {
				bottom: 143px; }
			@include mq($max-width: 1023px) {
