				bottom: 165px; }
			@include mq($max-width: 413px) {
				bottom: 155px; }
			@include mq($max-width: 374px) {
				right: 5px;
				bottom: 140px; } }
		.katalog__title {
			display: flex;
			justify-content: space-between;
			align-items: center;
			margin-top: 10px;
			padding-right: 47px;
			margin-bottom: 10px;
			@include mq($max-width: 1359px) {
				padding-right: 35px; }
			@include mq($max-width: 767px) {
				padding-right: 47px; }
			@include mq($max-width: 374px) {
				padding-right: 0px; }
			h4 {
				font-weight: 400;
				font-size: 20px;
				line-height: 125.69%; }
			svg {
				@include mq($max-width: 767px) {
					position: absolute;
					right: 10px;
					top: 15px; }
				@include mq($max-width: 374px) {
					right: 10px;
					top: 15px; } } }
		.katalog__category {
