.bonuses {
    margin-top: 40px;
    position: relative;
    @include mq($max-width: 767px) {
        display: flex;
        gap: 20px;
        flex-direction: column;
        margin-top: 20px; }
    &__stock {
        @include mq($max-width: 767px) {
            display: none; } }
    &__mobile {
        display: none;
        @include mq($max-width: 767px) {
            display: block;
            max-width: 100%;
            width: 100%;
            border-radius: 15px; } }
    &__absolute {
        position: absolute;
        @include mq($max-width: 1279px) {
            display: none; } }
    .btn {
        max-width: 175px;
        height: 62px; }
    &__questions {
        margin-top: -70px;
        position: relative;
        @include mq($max-width: 1279px) {
            margin-top: -45px; }
        @include mq($max-width: 1023px) {
            margin-top: -30px; }
        @include mq($max-width: 819px) {
            margin-top: -25px; }
        @include mq($max-width: 767px) {
            margin-top: 0px; }
        .bonuses__title {
            position: absolute;
            left: 45px;
            bottom: 45px;
            font-weight: 900;
            font-size: 60px;
            line-height: 95.69%;
            z-index: 4;
            @include mq($max-width: 1359px) {
                font-size: 50px; }
            @include mq($max-width: 1279px) {
                font-size: 38px;
                left: 25px;
                bottom: 25px; }
            @include mq($max-width: 1023px) {
                top: 65px;
                left: 10px; }
            @include mq($max-width: 767px) {
                font-size: 7vw;
                top: 10px; }
            @include mq($max-width: 374px) {
                font-size: 6vw; } }
        .bonuses__stock {
            max-width: 100%;
            width: 100%; }
        .bonuses__absolute {
            position: absolute;
            top: -191px;
            left: -194px;
            z-index: 2;
            @include mq($max-width: 1679px) {
                top: -190px;
                left: -200px;
                max-width: 1636px; }
            @include mq($max-width: 1439px) {
                top: -172px;
                left: -170px;
                max-width: 1511px; }
            @include mq($max-width: 1359px) {
                top: -165px;
                left: -167px;
                max-width: 1430px; } }
        .bonuses__form {
            max-width: 338px;
            width: 100%;
            display: flex;
            gap: 10px;
            align-items: end;
            flex-direction: column;
            @include mq($max-width: 1279px) {
                max-width: 280px; }
            @include mq($max-width: 1023px) {
                max-width: 100%;
                flex-direction: row; }
            @include mq($max-width: 767px) {
                flex-direction: column;
                gap: 5px; } }
        form {
            display: flex;
            position: absolute;
            right: 45px;
            bottom: 45px;
            gap: 28px;
            z-index: 4;
            align-items: end;
            @include mq($max-width: 1279px) {
                right: 25px;
                bottom: 25px; }
            @include mq($max-width: 1023px) {
                right: 10px;
                bottom: 10px;
                gap: 10px; }
            @include mq($max-width: 767px) {
                flex-direction: column-reverse;
                margin: 0 auto;
                left: 10px;
                right: 10px;
                gap: 5px; }
            .btn {
                min-width: 175px;
                background: rgba(0, 0, 0, 0.17);
                backdrop-filter: blur(24px);
                border-radius: 12px;
                font-weight: 500;
                font-size: 18px;
                margin: 0 auto;
                @include mq($max-width: 413px) {
                    height: 45px; } }

            textarea {
                max-width: 100%;
                width: 100%;
                background: rgba(193, 193, 193, 0.22);
                backdrop-filter: blur(24px);
                border-radius: 12px;
                padding: 22px;
                font-weight: 400;
                font-size: 16px;
                line-height: 125.69%;
                color: rgba(255, 255, 255, 0.7);
                min-height: 204px;
                margin-top: 16px;
                resize: none;
                @include mq($max-width: 1359px) {
                    min-height: 160px; }
                @include mq($max-width: 1279px) {
                    min-height: 135px; }
                @include mq($max-width: 767px) {
                    margin-top: 0;
                    min-height: 100px; }
                @include mq($max-width: 359px) {
                    min-height: auto;
                    max-height: 65px;
                    padding: 8px; } }
            input {
                max-width: 100%;
                width: 100%;
                height: 62px;
                background: rgba(193, 193, 193, 0.22);
                backdrop-filter: blur(24px);
                border-radius: 12px;
                padding: 0 22px;
                font-weight: 400;
                font-size: 16px;
                line-height: 125.69%;
                color: rgba(255, 255, 255, 0.7);
                @include mq($max-width: 413px) {
                    height: 45px; }
                @include mq($max-width: 359px) {
                    padding: 0 8px; }
                &::placeholder {
                    font-weight: 400;
                    font-size: 16px;
                    line-height: 125.69%;
                    color: rgba(255, 255, 255, 0.7); } } } }
    &__referals {
