            @include mq($max-width: 1439px) {
                max-width: 702px; }
            @include mq($max-width: 1359px) {
                max-width: 652px; }
            @include mq($max-width: 1279px) {
                max-width: 512px; }
            @include mq($max-width: 1023px) {
                max-width: 402px; }
            @include mq($max-width: 819px) {
                max-width: 372px; } }
        .bonuses__absolute {
            right: -55px;
            top: -37px;
            @include mq($max-width: 1679px) {
                right: -14px;
                top: -35px;
                max-width: 445px; }
            @include mq($max-width: 1439px) {
                right: 20px;
                top: -33px;
                max-width: 427px; }
            @include mq($max-width: 1359px) {
                right: 71px;
                top: -33px;
                max-width: 399px; } } }

    &__discounts {
        position: absolute;
        right: 0;
        top: 0;
        @include mq($max-width: 767px) {
            position: relative; }
        .bonuses__title {
            position: absolute;
            left: 45px;
            top: 45px;
            font-weight: 900;
            font-size: 60px;
            line-height: 95.69%;
            max-width: 433px;
            @include mq($max-width: 1359px) {
                font-size: 55px; }
            @include mq($max-width: 1279px) {
                font-size: 38px;
                left: 25px;
                top: 25px;
                max-width: 340px; }
            @include mq($max-width: 1023px) {
                top: 10px;
                left: 10px; }
            @include mq($max-width: 767px) {
                font-size: 7vw; }
            @include mq($max-width: 374px) {
                font-size: 6vw;
                line-height: 110%; } }
        .bonuses__desk {
            position: absolute;
            left: 180px;
            bottom: 180px;
            font-weight: 500;
            font-size: 22.7121px;
            line-height: 131.19%;
            color: rgba(255, 255, 255, 0.8);
            max-width: 190px;
            z-index: 3;
            @include mq($max-width: 1279px) {
                left: 110px;
                bottom: 110px; }
            @include mq($max-width: 1023px) {
                left: 80px;
                bottom: 80px; }
            @include mq($max-width: 767px) {
                left: 10px; }
            @include mq($max-width: 413px) {
                bottom: 65px; }
            @include mq($max-width: 374px) {
                font-size: 6vw;
                line-height: 100%; } }
        .flex {
            align-items: center;
            position: absolute;
            gap: 28px;
            right: 45px;
            bottom: 45px;
            z-index: 3;
            @include mq($max-width: 1279px) {
                right: 25px;
                bottom: 25px;
                gap: 10px; }
            @include mq($max-width: 1023px) {
                right: 10px;
                bottom: 10px; }
            @include mq($max-width: 767px) {
                left: 10px; }
            input {
