                max-width: 398px; }
            @include mq($max-width: 819px) {
                max-width: 368px; } }
        .bonuses__absolute {
            right: -47px;
            top: -22px;
            @include mq($max-width: 1679px) {
                right: -45px;
                top: -21px;
                max-width: 155px; }
            @include mq($max-width: 1439px) {
                right: -45px;
