// main page elements
body {
	font-size: 14px;
	color: white;
	font-family: 'Rubik', sans-serif;
	font-weight: 400;
	background: white;
	overflow-x: hidden;
	background: #000205;
 }	// +scrollbar(10px, auto, $yellow, $gray2, $dark, null)

a {
	color: white; }

.page-container {
	max-width: 1920px;
	width: 100%;
	margin: 0 auto;
	overflow: hidden; }

.container {
	position: relative;
	max-width: 1440px;
	width: 100%;
	padding: 0;
	margin: 0 auto;
	@include mq($max-width: 1440px) {
		padding: 0 30px;
		max-width: 100%; }
	@include mq($max-width: 575px) {
		padding: 0 15px; } }

.flex {
	display: flex; }

.btn {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	border-radius: 12px;
	color: white; }

.title-block {
	margin-bottom: 32px;
	font-weight: 900;
	font-size: 38px;
	line-height: 125.69%;
	@include mq($max-width: 767px) {
		font-size: 32px;
		line-height: 100%;
		margin-bottom: 17px; }
	@include mq($max-width: 374px) {
		font-size: 24px; } }

.title-row {
	display: flex;
	gap: 30px;
	margin-bottom: 32px;
	align-items: center;
	@include mq($max-width: 767px) {
		justify-content: space-between; }
	@include mq($max-width: 374px) {
		margin-bottom: 20px;
		flex-direction: column;
		gap: 0px;
		align-items: flex-start; }
	h3 {
		margin-bottom: 0; }
	a {
		margin-top: 10px;
		font-weight: 700;
		font-size: 18px;
		line-height: 125.69%;
		color: #F59502;
		@include mq($max-width: 374px) {
			font-size: 15px;
			margin-top: 5px; } } }

.swiper-pagination {
	display: none;
	@include mq($max-width: 1279px) {
		display: block; } }
.swiper-pagination-bullet {
	width: 13px;
	height: 13px;
	margin: 0 5px;
	background: rgba(255, 255, 255, 0.1);
	opacity: 1; }
.swiper-pagination-bullet-active {
	opacity: 1;
	background: rgba(255, 255, 255, 0.6); }
